import "./style.css";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { PointerLockControls } from "three/examples/jsm/controls/PointerLockControls.js";

import * as dat from "dat.gui";
import Stats from "stats-js";

import modelLoaderGLTF from "./utils/modelLoaderGLTF";
import loadTexture from "./utils/textureLoader";

// import stanKubrick from "url:../static/models/stanlyMaBoi/Stan.gltf";

export default class Sketch {
  constructor(options) {
    this.container = options.dom;

    this.sizes = {
      width: window.innerWidth,
      height: window.innerHeight,
    };

    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setSize(this.sizes.width, this.sizes.height);
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.renderer.setClearColor("#262837", 1);
    this.renderer.sRGBEncoding = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    this.renderer.physicallyCorrectLights = true;
    this.renderer.autoClear = false;
    this.renderer.antialias = false;
    this.renderer.physicallyCorrectLights = true;
    this.container.appendChild(this.renderer.domElement);

    this.envFBO = new THREE.WebGLRenderTarget(
      this.sizes.width,
      this.sizes.height
    );

    this.backfaceFBO = new THREE.WebGLRenderTarget(
      this.sizes.width,
      this.sizes.height
    );

    this.clock = new THREE.Clock();
    this.time = 0;

    this.setup();
  }

  async setup() {
    this.stat = new Stats();
    this.stat.showPanel(0);
    document.body.appendChild(this.stat.dom);

    this.showSettings = true;

    // this.rotateMesh = true;

    this.debugObject = {
      rendererParams: {},
      controllerParams: {},
    };

    this.allModelsUrl = {
      blueCircleLogo: "/Models/blueCircleLogo.gltf",
    };

    this.mouse = {
      x: 0,
      y: 0,
      prevX: 0,
      prevY: 0,
      vX: 0,
      vY: 0,
    };

    this.createScene();

    await this.init();

    this.resize();

    this.setupResize();

    await this.settings();

    this.render();

    this.mouseEvents();
  }

  async createScene() {
    this.scene = new THREE.Scene();

    this.setupCameras();

    this.setupLights();

    this.setupPointerLockControls();
  }

  async setupCameras() {
    this.camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );

    this.camera.position.set(-5, 30, 350);
    this.scene.add(this.camera);
  }

  async setupControls() {
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enabled = true;
  }

  async setupPointerLockControls() {
    this.controls = new PointerLockControls(
      this.camera,
      this.renderer.domElement
    );

    window.addEventListener(
      "click",
      () => {
        this.controls.lock();
      },
      false
    );

    this.keyboard = [];
    addEventListener("keydown", (e) => {
      this.keyboard[e.key] = true;
    });
    addEventListener("keyup", (e) => {
      this.keyboard[e.key] = false;
    });
    this.speed = 1.7;
  }
  processKeyboard() {
    if (this.keyboard["w"]) {
      this.controls.moveForward(this.speed);
    }
    if (this.keyboard["s"]) {
      this.controls.moveForward(-this.speed);
    }
    if (this.keyboard["a"]) {
      this.controls.moveRight(-this.speed);
    }
    if (this.keyboard["d"]) {
      this.controls.moveRight(this.speed);
    }
    if (this.keyboard["b"]) {
      // camera.position.y = cameraPositionY + 20
      for (var i = 1; i <= 3; i++) {
        // console.log(cameraPositionY)

        camera.position.y += i ^ 2;

        console.log(camera.position.y);
      }
    }
  }
  async setupLights() {
    this.ambLight = new THREE.AmbientLight("#b9d5ff");
    this.scene.add(this.ambLight);

    this.moonLight = new THREE.DirectionalLight("#b9d5ff", 0.4);
    this.moonLight.position.set(4, 15, -2);
    this.scene.add(this.moonLight);

    this.spotLight = new THREE.SpotLight(0xffffff, 10, 50, Math.PI);
    this.spotLight.position.set(20, 50, 20);
    this.scene.add(this.spotLight);
  }

  async loadTextures() {
    this.floorTexture = await loadTexture("/textures/matcaps/5.png");

    this.domeTexture = await loadTexture("/textures/bricks/normal.jpg");
  }

  async loadEnvMap() {
    this.cubeTextureLoader = new THREE.CubeTextureLoader();
    this.environmentMap = this.cubeTextureLoader.load([
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
    ]);

    this.environmentMap.encoding = THREE.sRGBEncoding;
    this.scene.background = this.environmentMap;
    this.scene.environment = this.environmentMap;
  }

  async init() {
    this.loadEnvMap();
    this.loadTextures();
    await this.loadBasicGroundMesh();
    await this.loadBasicFloor();

    this.blueCircleLogo = await this.loadBlueCircleLogoModel();
    this.scene.add(this.blueCircleLogo);

    this.dummy = await this.loadDummy();
    this.scene.add(this.dummy);

    this.dudeDummy = await this.loadDudeDummy();
    this.scene.add(this.dudeDummy);

    this.dome = await this.loadDome();
    this.scene.add(this.dome);

    /* 
    this.bench = await this.loadBench();
    this.scene.add(this.bench);
    this.lukas = await this.loadLukas();
    this.scene.add(this.lukas);
     */
  }

  async loadBasicGroundMesh() {
    this.basicMatty = new THREE.MeshBasicMaterial({
      color: new THREE.Color("#ccc"),
      wireframe: true,
    });
    this.groundGeo = new THREE.PlaneBufferGeometry(100, 100, 16, 16);
    this.ground = new THREE.Mesh(this.groundGeo, this.basicMatty);
    this.ground.rotation.set(-Math.PI / 2, 0, 0);
    this.scene.add(this.ground);
  }

  async loadBasicFloor() {
    let floorGeo = new THREE.PlaneGeometry(1300, 1300);
    let floorMat = new THREE.MeshPhongMaterial({
      // color: "red",
      //texture not loaded yet
      map: this.floorTexture,
    });

    this.floor = new THREE.Mesh(floorGeo, floorMat);

    //clarify with neil the intention behind this code

    this.floor.geometry.setAttribute(
      "uv2",
      new THREE.Float32BufferAttribute(this.floor.geometry.attributes.uv, 2)
    );

    this.floor.rotation.x = -Math.PI * 0.5;
    this.floor.position.y = -3;
    this.floor.receiveShadow = true;
    this.scene.add(this.floor);
  }

  async loadBlueCircleLogoModel() {
    let logoMaterial = new THREE.MeshStandardMaterial({
      color: 0x824848,
      roughness: 0.2,
      metalness: 0.8,
    });

    let { model } = await modelLoaderGLTF("/Models/blueCircleLogo.gltf");

    model.traverse((child) => {
      if (child.isMesh) {
        child.material.map = true;
        child.material.opacity = 1;
        child.material = logoMaterial;
      }
    });

    model.scale.set(40, 40, 40);
    model.position.set(0, -10, 0);

    return model;
  }

  async loadDummy() {
    let { model } = await modelLoaderGLTF("/Dummy/scene.gltf");

    model.scale.set(40, 40, 40);

    model.position.set(0, 0.1, 150);

    return model;
  }

  async loadDudeDummy() {
    let { model } = await modelLoaderGLTF("/dudeDummy/scene.gltf");

    model.scale.set(0.4, 0.4, 0.4);
    model.rotation.y = 1.4;

    model.position.set(140, 0.1, 18);

    return model;
  }

  async loadDome() {
    let { model } = await modelLoaderGLTF("/Models/dome.gltf");

    model.traverse((child) => {
      if (child.isMesh) {
        child.material.map = this.domeTexture;
      }
    });

    model.scale.set(40, 40, 40);

    model.position.set(0, -7, 0);

    return model;
  }

  /*  async loadBench() {
    let { model } = await modelLoaderGLTF("/Bench/scene.gltf");

    model.scale.set(0.3, 0.3, 0.3);

    model.position.set(0, 1, -550);

    return model;
  }

  async loadLukas() {
    let { model } = await modelLoaderGLTF("/Lukas/scene.gltf");

    model.scale.set(30, 30, 30);
    model.position.set(-10, 0.1, 100);

    return model;
  } */

  settings() {
    let that = this;
    this.settings = {
      sampleControl: 0,
    };

    if (!this.showSettings) return;
    this.gui = new dat.GUI();

    this.rendererProperties = this.gui.addFolder("Renderer Properties");

    this.cameraProperties = this.gui.addFolder("Camera Properties");

    this.controllerProperties = this.gui.addFolder("Controller Properties ");
  }

  mouseEvents() {
    window.addEventListener("mousemove", (e) => {
      this.mouse.x = e.clientX / this.sizes.width;
      this.mouse.y = e.clientY / this.sizes.height;

      this.mouse.vX = this.mouse.x - this.mouse.prevX;
      this.mouse.vY = this.mouse.y - this.mouse.prevY;

      this.mouse.prevX = this.mouse.x;
      this.mouse.prevY = this.mouse.y;
    });
  }

  render() {
    this.processKeyboard();
    this.time = this.clock.getDelta();
    this.stat.update();

    this.renderer.render(this.scene, this.camera);

    this.renderer.render(this.scene, this.camera);
    // this.controls.update();
    window.requestAnimationFrame(this.render.bind(this));
    this.stat.end();
  }

  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
  }

  resize() {
    this.sizes.width = window.innerWidth;
    this.sizes.height = window.innerHeight;
    this.renderer.setSize(this.sizes.width, this.sizes.height);
    this.camera.aspect = this.sizes.width / this.sizes.height;

    // image cover please uncomment this when using ortho camera
    /* this.imageAspect = 1;
    let a1;
    let a2;
    if (this.sizes.height / this.sizes.width > this.imageAspect) {
      a1 = (this.sizes.width / this.sizes.height) * this.imageAspect;
      a2 = 1;
    } else {
      a1 = 1;
      a2 = this.sizes.height / this.sizes.width / this.imageAspect;
    }

    this.material.uniforms.resolution.value.x = this.sizes.width;
    this.material.uniforms.resolution.value.y = this.sizes.height;
    this.material.uniforms.resolution.value.z = a1;
    this.material.uniforms.resolution.value.w = a2; */

    this.camera.updateProjectionMatrix();
  }

  lerp(x, y, t) {
    return (1 - t) * x + t * y;
  }
}

const canvas = new Sketch({
  dom: document.getElementById("webgl"),
});
